*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 62.5%;
  background: -webkit-radial-gradient(bottom, ellipse, #1b2735 0%, #090a0f 100%);
  background: -o-radial-gradient(bottom, ellipse, #1b2735 0%, #090a0f 100%);
  background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
  height: 100%; }
  @media only screen and (max-width: 75em) {
    html {
      font-size: 50%; } }

body {
  font-family: Inter, cursive;
  font-weight: Medium;
  line-height: 1.2;
  color: #FFFFFF; }

.container {
  display: grid;
  grid-template-rows: 7rem -webkit-min-content repeat(4, -webkit-min-content);
  grid-template-rows: 7rem min-content repeat(4, min-content);
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 5rem;
  margin: 0 10%;
  background-color: #222222;
  -webkit-box-shadow: 0 10px 10px #FFF;
          box-shadow: 0 10px 10px #FFF;
  padding: 4rem;
  font-size: 3rem; }

